import mixins from "~/utils/mixins";
import {common} from "~/utils/api/url";
import {MessageError, MessageSuccess} from "~/utils/utils";
import MyElTree from "~/components/tree/MyElTree";
import DrawerLayoutRight from "~/components/utils/dialog/DrawerLayoutRight";
import MyInputButton from "~/components/search/MyInputButton";
import MyCascader from "~/components/utils/select/MyCascader";
import MySexSelect from "~/components/utils/MySexSelect";
export default {
  components: {MySexSelect, MyElTree, MyCascader},
  mixins: [mixins],
  data(){
    return {
      searchCollege: '',
      searchMajor: '',
      searchGrade: '',
      searchClass: '',
      tableData: [],
      tableDataTitles: [],
      searchKey: '',
      appName: '',
      dialogServerDetail: false,
      formId: '',
      linkId: '',
      detailData: {},
      detailApplyContentData: [],
      searchAuditStatus: '',
      queryApplyListType: 0,
      appletId: '',
      searchStatusInfo: '',
      searchCommitInfo: '',
      searchTimeData: [],
      searchCascader: [],
      defaultExpandedKeys: [],
      currentNodeKey: '',
      filtersStatusType: [
        {text: '线下环节',value: "0"},
        {text: '线上环节',value: "1"}
      ],
      filtersCommitType: [
        {text: '已完成',value: 1},
        {text: '未完成',value: 0}
      ]
    }
  },
  created() {
    this.initLink();
  },
  methods: {
    init(){
      let params = {
        page: this.page,
        num: this.num,
        collegeId: this.searchCollege,
        majorId: this.searchMajor,
        grade: this.searchGrade,
        classId: this.searchClass,
        linkType: this.searchStatusInfo,
        status: this.searchCommitInfo,
        searchKey: this.searchKey,
        linkId: this.currentNodeKey
      };

      if (this.searchTimeData && this.searchTimeData.length > 0){
        params['startTime'] = this.$moment(this.searchTimeData[0]).format("YYYY-MM-DD");
        params['endTime'] = this.$moment(this.searchTimeData[1]).format("YYYY-MM-DD");
      }

      let applyContentArr = [];
      let applyContent = [];
      this.$axios.get(common.enroll_form_data_page, {params: params}).then(res => {
        if (res.data.data){
          if (res.data.data.list && res.data.data.list.length > 0 && res.data.data.list[0].applyContent){
            applyContentArr = JSON.parse(res.data.data.list[0].applyContent);
          }

          for (let i = 0; i < res.data.data.list.length; i++){
            if (res.data.data.list[i].applyContent){
              let applyContent = JSON.parse(res.data.data.list[i].applyContent);
              res.data.data.list[i]['applyContentObj'] = JSON.parse(res.data.data.list[i].applyContent);
            }
          }

          this.applyContentArr = applyContentArr;
          this.tableDataTitles = res.data.data.title;
          this.tableData = res.data.data.list;
          this.total = res.data.data.total;
          this.num = res.data.data.num;
          this.page = res.data.data.page;
        }
      });
    },
    async initLink(){
      this.mainMenu = 2;
      this.pageDetail = 1;
      this.appName = '';
      this.appletId = '';
      this.formId = '';

      await this.getAppletServerInfo(this.appName);
      let data = this.dataAppletServer;
      if (data && data[0]['children']){
        this.linkId = data[0]['children'][0].id;
        this.defaultExpandedKeys = [data[0].id, data[0]['children'][0].id];
        this.currentNodeKey = data[0]['children'][0].id;
      }
      this.init();
    },
    sizeChange(event){
      this.page = 1;
      this.num = event;
      this.init();
    },
    currentPage(event){
      this.page = event;
      this.init();
    },
    jumpPage(data){
      this.page = data;
      this.init();
    },
    closeDrawerDialog(event){
      this.dialogServerDetail = event;
    },
    cancelDrawDialog(){
      this.dialogServerDetail = false;
    },
    nodeClick(data){
      if (data.unit == 2){
        //this.processid = data.id;
        this.currentNodeKey = data.value;
        this.page = 1;
        this.init();
      }
    },
    handleChangeTime(data){
      this.searchTimeData = data;
    },
    expandInfo(){
      let url = "";
      let params = {
        page: 1,
        num: 86400,
        collegeId: this.searchCollege,
        majorId: this.searchMajor,
        grade: this.searchGrade,
        classId: this.searchClass,
        linkType: this.searchStatusInfo,
        status: this.searchCommitInfo,
        searchKey: this.searchKey,
        linkId: this.linkId
      };
      params = this.$qs.stringify(params);
      url = common.enroll_form_data_export;
      window.open(url+"?"+params, "_self");
    },
    statusInfo(item, type){
      let params = {
        id: item._id,
        status: type,
      };
      let url = common.enroll_form_data_set_status;
      params = this.$qs.stringify(params);
      this.$axios.post(url, params).then(res => {
        if (res.data.code == 200){
          this.init();
          MessageSuccess(res.data.desc);
        }else {
          MessageError(res.data.desc);
        }
      });
    },
    detailInfo(item){
      this.detailData = item;
      console.log(item.dataContent);
      if (item.dataContent  && item.dataContent != "[]"){
        let ruleList = [];
        //let detailApplyContentData = JSON.parse(item.dataContent);
        this.detailApplyContentData = this.setRuleChild(JSON.parse(item.dataContent), ruleList);
        //this.detailApplyContentData = JSON.parse(item.dataContent);
      }
      this.dialogServerDetail = true;
    },
    setRuleChild(rule, ruleList){
      let obj = {};
      for (let i = 0; i < rule.length; i++){
        if (rule[i]['children'] && rule[i]['children'].length > 0){
          this.setRuleChild(rule[i]['children'], ruleList);
          continue;
        }else {
          if (rule[i].field){
            obj = {
              field: rule[i].field,
              title: rule[i].title,
              type: rule[i].type,
              value: rule[i].value
            }
            ruleList.push(obj);
          }
        }
      }
      return ruleList;
    },
    search(data){
      this.searchKey = data.input;
      this.page = 1;
      this.init();
    },
    handleCascaderChange(data){
      this.searchCollege = "";
      this.searchMajor = "";
      this.searchGrade = "";
      this.searchClass = "";
      if (data.length == 1){
        this.searchCollege = data[0];
      }else if (data.length == 2){
        this.searchCollege = data[0];
        this.searchMajor = data[1];
      }else if (data.length == 3){
        this.searchCollege = data[0];
        this.searchMajor = data[1];
        this.searchGrade = data[2];
      }else if (data.length == 4){
        this.searchCollege = data[0];
        this.searchMajor = data[1];
        this.searchGrade = data[2];
        this.searchClass = data[3];
      }
      this.page = 1;
      this.init();
    },
    fliterTable(value, row, column){
      for (let item in value){
        if (item == 'typeInfo'){
          this.searchStatusInfo = value[item][0];
        }else if (item == 'commitInfo'){
          this.searchCommitInfo = value[item][0];
        }
      }
      this.init();
    }
  }
}
